.channel-add-info{
    border-top: 1px solid #000;border-bottom: 1px solid #000;line-height: 1.4;display: flex;justify-content: space-between;padding: 20px 40px;
    small{font-size: 12px;}
    del{color: #c7c7c7;}
    strong{font-size: 20px;font-weight: 500;display: block;}
    .plan{
        strong{margin-bottom: 5px;}
    }
    .price{text-align: center;
        strong{margin-top: 5px;}
    }
    @include media-breakpoint-down(md){
        padding: 10px;
    }
}
.channel-add-desc{
    font-size: 14px;margin-top: 13px;
}

.discount{
    text-decoration: none;position: relative;padding-left: 10px;padding-right: 10px;color: #c7c7c7;  
    &::before{content: '';display: block;width: 100%;height: 1px;background: #8c8c8c;position: absolute;left: 0;top: 50%;}
    &::after{content: '';display: block;width: 15px;height: 1px;background: #8c8c8c;position: absolute;right: 0;top: 50%;transform: rotate(-45deg) translate(-2px, 5px)}
}

.filter{
    margin-bottom: 37px;
    .filter-head{
        border-bottom: 1px solid #000;display: flex;align-items: center;padding-bottom: 10px;
        h3{font-weight: 500;font-size: 16px;}
    }
    .filter-body{border-bottom: 1px solid #8c8c8c;padding: 20px 0;}
    &.has-collapsed{
        margin-bottom: 25px;
        .filter-head-container {border-bottom: 1px solid #000;}
        .filter-head{border-bottom:0;}
        a{text-decoration: none;}
    }
    
    @include media-breakpoint-down(lg){
        .condition{
            .dropdown{width: 100%;}
        }
        .recently{
            > .d-flex{margin-top: 20px;padding-top: 20px;border-top: 1px solid #8c8c8c;}
        }
        .filter-body{
            border-bottom:0;
        }
        // &.has-collapse{margin-bottom: 25px;}
    }
}

.chart{
    border-top: 1px solid #000;padding-top: 3.125rem;
    @include media-breakpoint-down(md){
        padding-top: 17px;
    }
}
.chart-legend{
    display: inline-flex;align-items: center;font-size: 14px;margin-bottom: 1rem;
    .dot{width: 5px;height: 5px;border-radius: 50%;margin-right: 7px;}
    @include media-breakpoint-down(md){
        font-size: 12px;
    }
}


.card{
    &.remove-channel{
        position:relative;
        padding:25px 37px 25px 30px;
        margin-bottom: 25px;
        button{
            position: absolute; 
            top:10px;right: 10px; width:10px; height:10px;
            i{
                width:10px; height:10px;
                vertical-align: top;
            }
        }
        .channel-img{
            width:60px;
            height:60px;
            object-fit: cover;
            object-position: center;
            border-radius: 50%;
        }
        .channel-name{
            font-size: 20px;
            font-weight: 500;
        }
        .subscriber{
            font-size: 14px;
        }
        @include media-breakpoint-down(md){
            padding:19px 37px 19px 30px;
            margin-bottom: 10px;
            .channel-img{
                width: 52px;
                height: 52px;
            }
            .channel-name{
                font-size: 18px;
            }
            .subscriber{
                font-size: 12px;
            }
        }
    }

    &.channel-list{
        padding:43px 60px;
        text-decoration: none;
        .thumbnail{
            min-width: 320px;
            position: relative;
            padding-top: 56%;
            width: 100%;
            height: 0;
            img{
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
            }
        }
        .channel-img{
            border-radius: 50%;
            width: 60px; height: 60px; object-fit: cover; object-position: center;
        }
        .channel-name{
            font-size: 20px; font-weight: 500; margin-right: 10px; margin-left: 20px;
        }
        .channel-title{
            font-size: 20px; font-weight: 500;
        }
        .channel-desc{
            span{
                padding-right:30px;
                vertical-align: middle;
            }
        }
        @include media-breakpoint-down(lg){
            padding:30px 10px;
            .thumbnail{min-width: auto;}
            .channel-img{width: 42px; height:42px;}
            .channel-name{
                font-size: 16px;
                margin-right: 7px;
                margin-left: 10px;
            }
            .channel-subscriber{font-size: 12px;}
            .channel-title{
                font-size: 20px; font-weight: 500;
                white-space: initial;
            }
            .channel-desc{
                span{
                    line-height: 18px; font-size: 12px;
                }
            }
        }
    }
}

.sort{
    display: flex;
    align-items: center;
    margin-right:-11px;
    .bar{
        height:18px;
        background: #000;
    }
    li{
        a{
            display: flex;
            align-items: center;
            margin:0 11px;
            text-decoration: none;
        }
        &.active{
            a{
                color:$danger;
                i{
                    background-image: url('../images/icon-sort-active.svg');
                }
            }
        }
    }
    @include media-breakpoint-down(md){
        margin-right: -6px;
        .bar{
            height:16px;
        }
        li{
            a{margin:0 6px;}
            i{width:16px; height:16px;}
        }
    }
}

.frequency-use{
    display: flex;
    align-items: center;
    .bar{
        height:14px;
        background: #000;
    }
    li{
        font-size: 14px;
        &:first-of-type{
            margin-left: 15px;
        }
        + li{
            &::before{
                content: ',';
            }
        }
    }
}

.subscribe-channel-list{
    max-height: 104px;
    overflow-y: auto;
    @include media-breakpoint-down(md){
        max-height: 95px;
    }
}